
@import url('https://fonts.googleapis.com/css?family=Inconsolata');
body, html {
  margin: 0;
  padding: 0;
}
#app {
  background-color: #fcf7f3;
  font-family: 'Inconsolata',PingFangSC-Light,Ubuntu,Microsoft YaHei,Avenir,Helvetica Neue,sans-serif
}
.wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
h1 {
  color: #5fa1d2;
}
h2 {
  font-size: 18px;
}
input {
  outline: none;
  border: none;
  border-bottom: 2px solid #5fa1d2;
  padding: 8px 16px 3px;
  font-size: 20px;
}
ul {
  list-style: none;
}
li {
  margin-bottom: 20px;
}
label {
  margin-right: 20px;
  vertical-align: -webkit-baseline-middle;
}
span {
  color: #999;
  margin-left: 15px;
}
.github-corner:hover .octo-arm{-webkit-animation:octocat-wave 560ms ease-in-out;animation:octocat-wave 560ms ease-in-out
}
@-webkit-keyframes octocat-wave{
0%,100%{-webkit-transform:rotate(0);transform:rotate(0)
}
20%,60%{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)
}
40%,80%{-webkit-transform:rotate(10deg);transform:rotate(10deg)
}
}
@keyframes octocat-wave{
0%,100%{-webkit-transform:rotate(0);transform:rotate(0)
}
20%,60%{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)
}
40%,80%{-webkit-transform:rotate(10deg);transform:rotate(10deg)
}
}
@media (max-width:500px){
.github-corner:hover .octo-arm{-webkit-animation:none;animation:none
}
.github-corner .octo-arm{-webkit-animation:octocat-wave 560ms ease-in-out;animation:octocat-wave 560ms ease-in-out
}
}



